:root {
  --nav-shoadow: 0px 1px 6px 0px rgb(0, 0, 0, 0.1);
  --text-color: rgb(0, 0, 0, 0.88);
  --background-color: #ffffff;

  --table-bg: rgb(0, 127, 0, 0.1);
}

:root[class*='dark'] {
  --nav-shoadow: 0px 1px 6px 0px rgb(0, 0, 0, 0.9);
  --text-color: rgb(255, 255, 255, 0.85);
  --background-color: #141414;

  --table-bg: rgb(0, 127, 0, 0.25);
}

html,
body,
:global(#root) {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  font-weight: normal;
}

body {
  color: var(--text-color);
  background: var(--background-color);
  width: 100%;
  overflow-x: hidden;
}

/* tailwind preflight */
blockquote,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
figure,
p,
pre {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.html {
  font-size: 16px;
}

.spreadsheet-wrapper .spreadsheet {
  overflow: auto;
}

.diffhandsontable .add {
  background-color: #7fff7f;
}
.diffhandsontable .remove {
  background-color: #ff7f7f;
}
.diffhandsontable td.modify {
  background-color: #7f7fff;
}
.diffhandsontable td.conflict {
  background-color: red;
}
.diffhandsontable .spec {
  background-color: #aaa;
}
.diffhandsontable .move {
  background-color: #ffa;
}
.diffhandsontable .null {
  color: #888;
}
.diffhandsontable table {
  border-collapse: collapse;
}
.diffhandsontable td,
.diffhandsontable th {
  /* border: 1px solid #2d4068; */
  padding: 3px 7px 2px;
}
.diffhandsontable .header,
.diffhandsontable th {
  background-color: #aaf;
  font-weight: 700;
  padding-bottom: 4px;
  padding-top: 5px;
  text-align: left;
}
.diffhandsontable tr.header th {
  border-bottom: 2px solid #000;
}
.diffhandsontable .gap {
  color: #888;
}
.diffhandsontable td {
  empty-cells: show;
}
.diffhandsontable tr:first-child td {
  border-top: 1px solid #2d4068;
}
.diffhandsontable td:first-child {
  border-left: 1px solid #2d4068;
}

.ant-tabs > .ant-tabs-nav {
  margin-top: -1px !important;
}
